<template>
  <div class="cart" id="cart">
    <div class="cart-wrap">
      <div class="cart__block">
        <div class="cart__title">
          <h1>Корзина</h1>
        </div>
        <div class="cart__close" @click="closecart(), activeScrolling()">
          <svg
            width="27"
            height="27"
            viewBox="0 0 27 27"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <g clip-path="url(#clip0)">
              <path
                d="M24.6133 0.00572781L0 24.6121L2.38323 26.996L26.9965 2.38963L24.6133 0.00572781Z"
                fill="#A9A9A9"
              />
              <path
                d="M2.38713 0.00403785L0.00390625 2.38794L24.6172 26.9943L27.0004 24.6104L2.38713 0.00403785Z"
                fill="#A9A9A9"
              />
            </g>
            <defs>
              <clipPath id="clip0">
                <rect width="27" height="27" fill="white" />
              </clipPath>
            </defs>
          </svg>
        </div>
      </div>
      <CartItem
        v-for="(item, index) in cart_data"
        :key="item.article"
        :id="index"
        v-bind:cart_item_data="item"
        @deleteCart="deleteCart(index)"
        :class="{ 'last-item': index === cart_data.length - 1 }"
      />
      <!-- :class="userLastCount" -->
      <!-- v-bind:class="{'last-animal' : 'animal-${index}'? index === item.length - 1}" -->
      <div class="cart__fix">
        <div class="cart__price">
          <p>
            Общая стоимость: <span> {{ cartTotalCost }}</span> ₽
          </p>
        </div>

        <router-link :to="{ name: name }">
          <button
            type="submit"
            class="cart__btn"
            @click="closecart(), activeScrolling()"
          >
            Оформить заказ
          </button>
        </router-link>
      </div>
      <!-- <CartItem v-for="(item, index) in cart_data"
        :key="item.article"
        v-bind:cart_item_data="item"
        @deleteCart="deleteCart(index)" /> -->
    </div>
    <div class="mini-block-footer">
      <Footer />
    </div>
  </div>
</template>

<script>
import CartItem from "@/components/cart/cart-item.vue";
import Footer from "@/components/footer";
import { mapActions } from "vuex";
// import CartItem from './cart-item.vue';
export default {
  name: "cart",
  data() {
    return {
      name: "order",
    };
  },
  props: {
    cart_data: {
      type: Array,
      default() {
        return [];
      },
    },
    scrolled: {
      type: Boolean,
      default() {
        return "";
      },
    },
  },
  methods: {
    ...mapActions(["DELETE_FROM_CART"]),
    deleteCart(index) {
      this.$emit("deleteCart");
      // console.log(index);
      // this.DELETE_FROM_CART(index);
    },
    closecart() {
      this.$emit("closecart");
    },
    activeScrolling() {
      this.$emit("activeScrolling");
    },
    //  ShowCart() {
    //    if (this.show) {
    //      this.show = false;
    //    } else this.show = true;
    //  },
  },
  computed: {
    cartTotalCost() {
      let result = [];
      const pricerow = 1320;
      for (let item of this.cart_data) {
        result.push(item.price * item.section + pricerow * item.row);
      }
      result = result.reduce(function (sum, el) {
        return sum + el;
      });
      return result;
    },
    userLastCount(index) {
      // let a = this.cart_data[this.cart_data.length - 1].varElement.id;
      return {
        active: this.cart_data[this.cart_data.length - 1],
      };
      // return document.getElementById(this.cart_data[this.cart_data.length - 1]);
    },
  },

  components: {
    CartItem,
    Footer,
  },
};
</script>

